.App {
  text-align: center;
  display: flex;

  .App-Left {
    width: 20%;
    height: 100vh;
    background-color: #282c34;
    flex-shrink: 0;

    .info-header {
      padding: 20px;
      padding-top: 20px;

      .info-header-text {
        font-family: "思源黑体 CN";
        font-weight: 700;
        letter-spacing: 0.15em;
        background-image:
          linear-gradient(90deg,
            #f0a1a8 0%,
            #f09c5a 25%,
            #83cbac 50%,
            #f09c5a 75%,
            #f0a1a8 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 200%;
        animation: title-flow 2.5s linear infinite;
      }

      .info-header-name {
        font-size: 40px;
        margin-bottom: 5px;
      }

      .info-header-title {
        font-size: 20px;
        margin-bottom: 5px;
        text-align: left;
      }

      .info-header-desc {
        font-size: 10px;
        font-weight: 400;
        background-image:
          linear-gradient(10deg,
            #FFFFFF 0%,
            #FFFFFF 25%,
            #FFFFFFaa 50%,
            #FFFFFFaa 75%,
            #FFFFFF 100%);
        text-align: left;
        margin-bottom: 5px;
      }
    }

    .info-row {
      padding: 10px;
      margin-bottom: 10px;
      background-color: rgba(255, 255, 255, 0.02);
      text-align: left;
      color: white;

      .info-row-title {
        font-size: 15px;
        font-weight: 600;
        margin-bottom: 5px;
      }

      .info-row-subtitle {
        font-size: 13px;
        font-weight: 400;
      }

    }
  }

  .App-Right {
    width: 80%;
    height: 100%;
    color: #282c34;
    flex: 1;

    .iframe-content {
      width: 100%;
      height: 100vh;
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}